
@import url('https://fonts.googleapis.com/css?family=Raleway');
html, body, #app {
	font-family: 'Raleway', sans-serif;	
	width: 100%;
	height: 100%;
}

/*Transition Effect*/
.fade-enter-active,
.fade-leave-active {
  transition: all 0.2s ease;
}
.fade-enter,
.fade-leave-active {
  opacity: 0;
}




